import uniIcon from "@/components/uni-icon/uni-icon.vue";
import uniCollapse from "@/components/uni-collapse/uni-collapse.vue"
import uniCollapseItem from "@/components/uni-collapse-item/uni-collapse-item.vue"
export default {
	components:{
		uniIcon,
		uniCollapse,
		uniCollapseItem
	},
	data() {
		return {
			height:0,
			lostList:[]
		}
	},
	onLoad() {
		this.height = uni.getSystemInfoSync().windowHeight;
		uni.showLoading({mask:true});
		this.getData();
	},
	methods: {
		formatDate(now){ 
			let year=now.getFullYear();
			let month=now.getMonth()+1;
			let date=now.getDate();
			let hour=now.getHours();
			let minute=now.getMinutes();
			let second=now.getSeconds();
			if(month<10){
			month = '0' + month;
			};
			if(date<10){
			date = '0' + date;
			};
			if(hour<10){
			hour = '0' + hour;
			};
			if(minute<10){
			minute = '0' + minute;
			};
			if(second<10){
			second = '0' + second;
			};
			return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second; 
		},
		getData(){
			uni.request({
				url: this.apiServer + 'cabinetRentEmploymentRecord/getCabinetRecordForgeList',
				method: 'POST',
				header:{
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						if(!!res.data.data){
							let data = res.data.data;
							this.lostList = [];
							for(let i=0;i<data.length;i++){
								data[i].ForgetDate = this.formatDate(new Date(data[i].ForgetDate));
								if(data[i].cabinetOut){
									data[i].cabinetIcon = '../../static/lost/ok.png';
									data[i].cabinetType = '已取出';
								}else{
									data[i].cabinetIcon = '../../static/lost/later.png';
									data[i].cabinetType = '未取出';
								};
								this.getTelData(data[i].fkSpaceCharacterNo,data[i]);
							};
						}else{
							uni.showModal({
								title: '提示',
								content: '您无申请的物品遗忘信息',
								confirmText: '返回首页',
								showCancel: false,
								success: function (res) {
									if (res.confirm) {
										uni.navigateBack({url: '../index/index'});
									}
								}
							});
						}
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				},
				complete: (res) => {
					uni.hideLoading();
				}
			});
		},
		getTelData(No,data){
			uni.request({
				url: this.apiServer + 'spaceCharacter/getSpaceCharacterOne',
				method: 'POST',
				data:{
					spaceCharacterNo:No
				},
				header:{
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						data.Tel = res.data.data.spaceCharacterMobile;
						this.lostList.push(data);
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				}
			});
		},
		server(no){
			uni.makePhoneCall({
				phoneNumber: no
			});
		}
	}
}
